.main {
  background: #fff;
  padding: 30px;
  margin: 50px auto;
  max-width: 500px;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba( 0, 0, 0, 0.1);
}

.main h1 {
  font-size: 20px;
  text-align: center;
}

.form {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form input[type="text"] {
  height: 40px;
  padding: 0 20px;
  font-size: 20px;
  border: 1px solid #ccc;
  min-height: 80%;
}

.form button[type="submit"]{
  border: none;
  padding: 0 20px;
  height: 40px;
  background: #dc732b;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tarefas {
  margin-top: 30px;
}

.tarefas li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  transition: all 600ms;
}

.tarefas li:hover {
  background: #FF5794;
}

.tarefas li .edit {
  margin-right: 15px;
  color: #aaf0d1;
}

.tarefas li .winClose {
  color: red;
}

.tarefas li:hover .edit,
.tarefas li:hover .winClose {
  color: #000;
}

.tarefas li .edit,
.tarefas li .winClose {
  cursor: pointer;
  transition: all 600ms;
}

.tarefas li span {
  display: flex;
  align-items: center;
}
